@import url("https://fonts.googleapis.com/css2?family=Mulish:wght@200;300;400;500;600;700;800;900&display=swap");


.notification {
  position: relative;
  background: #EEEEEE;
  animation: 0.5s fadeInLeft;
  overflow: hidden;
  margin: 5px 0px;
  display: block;
  padding: 10px;
  padding-right: 45px
}

.notification p {
  margin: 0px
}

.notification.danger {
  background: #f8d7da
}

.notification.hide {
  animation: 0.5s fadeOutLeft;
  transform: translateX(calc(-100% - 20px))
}

.notification .notification_toggle {
  position: absolute;
  top: 50%;
  right: 15px;
  height: 27px;
  transform: translateY(-50%);
  line-height: 28px
}

.notification::after {
  content: "";
  background: #195498;
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  height: 3px;
  animation: 15s fillOut
}

@keyframes fillOut {
  0% {
    width: 100%
  }

  100% {
    width: 0%
  }
}

@keyframes fadeOutLeft {
  0% {
    transform: translateX(0px)
  }

  100% {
    transform: translateX(calc(-100% - 20px))
  }
}

@keyframes fadeInLeft {
  0% {
    transform: translateX(calc(-100% - 20px))
  }

  100% {
    transform: translateX(0px)
  }
}

